/*------------------------
mixin that calculates if text needs to be light or dark
depending on the background color passed.

From this W3C document: http://www.webmasterworld.com/r.cgi?f=88&d=9769&url=http://www.w3.org/TR/AERT#color-contrast

usage:
@include text-contrast($bgcolor)
      
Color brightness is determined by the following formula: 
((Red value X 299) + (Green value X 587) + (Blue value X 114)) / 1000
------------------------*/

@mixin text-contrast($n: #333) {
  $color-brightness: round(
    (red($n) * 299) + (green($n) * 587) + (blue($n) * 114) / 1000
  );
  $light-color: round(
    (red(#ffffff) * 299) + (green(#ffffff) * 587) + (blue(#ffffff) * 114) / 1000
  );

  @if abs($color-brightness) < ($light-color/1.70) {
    color: rgba(255, 255, 255, 1);
  } @else {
    color: rgba(0, 0, 0, 0.8);
  }
}

/*------------------------
 color factory 
  eg: contrast-ink($contrastvalue)
------------------------*/

@function contrast($n: #333) {
  $color-brightness: round(
    (red($n) * 299) + (green($n) * 587) + (blue($n) * 114) / 1000
  );
  $light-color: round(
    (red(#ffffff) * 299) + (green(#ffffff) * 587) + (blue(#ffffff) * 114) / 1000
  );

  @if abs($color-brightness) < ($light-color/1.70) {
    @return #ffffff;
  } @else {
    @return #000000;
  }
}

/*------------------------
 color factory 
  eg: @include paint($blue-grey-50, bg-blue-grey-50);
------------------------*/

@mixin paint($paint: #333333, $make: bg-blue-grey-50) {
  .#{$make} {
    background-color: $paint;
    @include text-contrast($paint);
    &:hover {
      @include text-contrast($paint);
    }
  }
}

@mixin brush($brush: #333, $make: red-50) {
  .#{$make} {
    color: $brush;
  }
}

//mixen for settings side buttons
@mixin set-settings($class-element: nav-function-fixed) {
  .#{$class-element} .btn-switch[data-class='#{$class-element}'] {
    @extend %set-settings;
  }
}

//mixen for settings side buttons
@mixin paint-gradient($paint: $fusion-500, $make: bg-fusion-gradient) {
  .#{$make} {
    background-image: -webkit-linear-gradient(
      250deg,
      rgba($paint, 0.7),
      transparent
    );
    background-image: linear-gradient(250deg, rgba($paint, 0.7), transparent);
  }
}

/* backface visibility */
@mixin backface-visibility($argument: none) {
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -ms-backface-visibility: hidden;
  backface-visibility: hidden;
}

/* generate theme button */
@mixin theme-button-color(
  $theme-fusion: none,
  $theme-primary: none,
  $theme-info: none,
  $theme-success: none,
  $theme-warning: none,
  $theme-danger: none
) {
  background-image: -webkit-linear-gradient(
    left,
    #{$theme-fusion},
    #{$theme-fusion} 70%,
    #{$theme-primary} 70%,
    #{$theme-primary} 76%,
    #{$theme-info} 76%,
    #{$theme-info} 82%,
    #{$theme-success} 82%,
    #{$theme-success} 88%,
    #{$theme-warning} 88%,
    #{$theme-warning} 94%,
    #{$theme-danger} 94%,
    #{$theme-danger} 94%,
    #{$theme-danger} 100%
  );
  background-image: -moz-linear-gradient(
    left,
    #{$theme-fusion},
    #{$theme-fusion} 70%,
    #{$theme-primary} 70%,
    #{$theme-primary} 76%,
    #{$theme-info} 76%,
    #{$theme-info} 82%,
    #{$theme-success} 82%,
    #{$theme-success} 88%,
    #{$theme-warning} 88%,
    #{$theme-warning} 94%,
    #{$theme-danger} 94%,
    #{$theme-danger} 94%,
    #{$theme-danger} 100%
  );
  background-image: -ms-linear-gradient(
    left,
    #{$theme-fusion},
    #{$theme-fusion} 70%,
    #{$theme-primary} 70%,
    #{$theme-primary} 76%,
    #{$theme-info} 76%,
    #{$theme-info} 82%,
    #{$theme-success} 82%,
    #{$theme-success} 88%,
    #{$theme-warning} 88%,
    #{$theme-warning} 94%,
    #{$theme-danger} 94%,
    #{$theme-danger} 94%,
    #{$theme-danger} 100%
  );
  background-image: linear-gradient(
    to right,
    #{$theme-fusion},
    #{$theme-fusion} 70%,
    #{$theme-primary} 70%,
    #{$theme-primary} 76%,
    #{$theme-info} 76%,
    #{$theme-info} 82%,
    #{$theme-success} 82%,
    #{$theme-success} 88%,
    #{$theme-warning} 88%,
    #{$theme-warning} 94%,
    #{$theme-danger} 94%,
    #{$theme-danger} 94%,
    #{$theme-danger} 100%
  );
}

// IE flexbox details:
//
// - Flexbox in IE 10:
//   https://msdn.microsoft.com/en-us/library/hh673531(v=vs.85).aspx
//
// - IE 11 flexbox changes (includes property/value names for IE 10)
//   https://msdn.microsoft.com/library/dn265027(v=vs.85).aspx

@mixin flexbox($important: false) {
  display: unquote("-ms-flexbox #{if($important, '!important', null)}");
  display: unquote("flex #{if($important, '!important', null)}");
}

@mixin inline-flexbox($important: false) {
  display: unquote("-ms-inline-flexbox #{if($important, '!important', null)}");
  display: unquote("inline-flex #{if($important, '!important', null)}");
}

@mixin align-content($value) {
  $ms-map: (
    flex-start: start,
    flex-end: end,
  );
  -ms-flex-line-pack: map-get($ms-map, $value) or $value;
  align-content: $value;
}

@mixin align-items($value) {
  $ms-map: (
    flex-start: start,
    flex-end: end,
  );
  -ms-flex-align: map-get($ms-map, $value) or $value;
  align-items: $value;
}

@mixin align-self($value) {
  $ms-map: (
    flex-start: start,
    flex-end: end,
  );
  -ms-flex-item-align: map-get($ms-map, $value) or $value;
  align-self: $value;
}

@mixin flex($value) {
  -ms-flex: $value;
  flex: $value;
}

@mixin flex-direction($value) {
  -ms-flex-direction: $value;
  flex-direction: $value;
}

@mixin flex-wrap($value) {
  $ms-map: (
    nowrap: none,
  );
  -ms-flex-wrap: map-get($ms-map, $value) or $value;
  flex-wrap: $value;
}

@mixin justify-content($value) {
  $ms-map: (
    flex-start: start,
    flex-end: end,
    space-around: distribute,
    space-between: justify,
  );
  -ms-flex-pack: map-get($ms-map, $value) or $value;
  justify-content: $value;
}

@mixin order($value) {
  -ms-flex-order: $value;
  order: $value;
}
